Office.onReady((info) => {
    if (info.host === Office.HostType.Outlook) {
        checkLoginStatus();
        document.getElementById("myButton").onclick = openDialog;
    }
});
let dialog;
async function openDialog() {
    // display overlay
    document.getElementById("overlay").style.display = "block";

    const groupOptions = Array.from(document.getElementById("group-data").options).map(option => ({
        value: option.value,
        text: option.text,
        selected: option.selected
    }));

    const clientOptions = Array.from(document.getElementById("client").options).map(option => ({
        value: option.value,
        text: option.text,
        selected: option.selected
    }));

    const noteTitle = document.getElementById("noteTitle").value;
    const noteContent = window.editorInstance.getData();
    const attachments = await getAttachments();
    const data = { groupOptions, clientOptions, noteTitle, noteContent, attachments };
    localStorage.setItem('current_login_userId', userId);
    localStorage.setItem("dialogData", JSON.stringify(data));

    Office.context.ui.displayDialogAsync(
        'https://outlookaddin.worksorted.com/newjob.html',
        { height: 70, width: 70 },
        function (asyncResult) {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                dialog = asyncResult.value;
                dialog.addEventHandler(Office.EventType.DialogEventReceived, closeDialog);
                dialog.addEventHandler(Office.EventType.DialogMessageReceived, function (arg) {
                    if (arg.message === "close") {
                        closeDialog();
                    } else if (arg.message === "newjob") {
                        console.log("New job added in CRM");
                        addActionInEmail();
                    }
                });
            } else {
                console.error("Failed to open dialog:", asyncResult.error);
            }
        }
    );
}

function addActionInEmail() {
    const message = {
        type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
        message: "This email was sent to Worksorted as a new job",
        icon: "/assets/icon-80.png",
        persistent: true,
    };

    // Show a notification message.
    Office.context.mailbox.item.notificationMessages.replaceAsync("action", message);
}

function closeDialog() {
    if (dialog) {
        dialog.close();
    }
    document.getElementById("overlay").style.display = "none"; // close overlay
}

let userId = 0;

async function checkLoginStatus() {
    // Get the stored unique ID (oid) from localStorage
    const storedUniqueId = localStorage.getItem('outlookuniqueId');

    try {
        // Send a request to your backend to check login status
        const response = await fetch('https://worksorted.com/outlookAddin/check_status.php', {
            method: 'POST',
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            },
            body: `access_token=${storedUniqueId}`
        });

        const data = await response.json();

        if (data.status === 'success') {
            userId = data.userId;
            // If the user is logged in, load CRM data
            loadCrmData(userId);
        } else {
            // If the user is not logged in, redirect to login page
            redirectToLogin();
        }
    } catch (error) {
        console.error('Error checking login status:', error);
        // If there's an error, redirect to login page as a fallback
        //redirectToLogin();
    }
}

function redirectToLogin() {
    // Clear the stored unique ID
    localStorage.removeItem('outlookuniqueId');

    // Redirect to the login page
    window.location.href = 'taskpane.html'; // Replace with your login page URL
}

function loadCrmData(userId) {
    // Your existing logic to load CRM data
    console.log('Loading CRM data...', userId);
    document.getElementById('crmData').innerHTML = '<h1>Welcome</h1>';
    document.getElementById('toggleSidebar').addEventListener('click', toggleSidebar);
    document.getElementById('arrow-left').addEventListener('click', toggleSidebar1);

    // group
    fetchGroups(userId).then(groups => {
        const groupSelect = document.getElementById("group-data");
        groupSelect.innerHTML = "";
        if (groups.length === 0) {
            groupSelect.innerHTML = '<option value="0">No Groups Found</option>';
            return;
        }
        groups.forEach(group => {
            const option = document.createElement("option");
            option.value = group.groupId;
            option.textContent = group.groupName;

            option.dataset.xplan_emailSuffix = group.pushEmailAddress || "";
            option.dataset.salesforce_emailSuffix = group.salesforceEmail || "";
            option.dataset.midwinter_emailSuffix = group.MidwinterEmail || "";

            groupSelect.appendChild(option);
        });
        groupSelect.value = groups[0].groupId;


        loadClients(groups[0].groupId);
    });
    loadEmailContent();
}

