// according to the selected category, load sub-categories
function loadSubCategories(groupId, categoryId) {
    fetch(`https://worksorted.com/outlookAddin/webgetSubNoteCategories.php?groupId=${groupId}&categoryId=${categoryId}`)
        .then(response => response.json())
        .then(subCategories => {
            const subCategorySelect = document.getElementById("sub-category-select");
            subCategorySelect.innerHTML = '<option value="">Select Sub-Category</option>'; // clear all options

            if (subCategories.length > 0) {
                subCategories.forEach(subCategory => {
                    const option = document.createElement("option");
                    option.value = subCategory.id;
                    option.textContent = subCategory.name;
                    subCategorySelect.appendChild(option);
                });
                subCategorySelect.style.display = 'inline'; // display Sub-category select box
            } else {
                subCategorySelect.style.display = 'none'; // for no sub-category, hide the select box
            }
        })
        .catch(error => {
            console.error("Error loading sub-categories:", error);
        });
}

