async function getAttachments() {
    return new Promise((resolve, reject) => {
        const item = Office.context.mailbox.item;

        if (item && item.attachments) {
            let attachments = item.attachments;

            // list to store attachment data
            let attachmentData = [];

            attachments.forEach((attachment) => {
                if (attachment.attachmentType === "file") {
                    // use getAttachmentContentAsync to get the attachment content
                    Office.context.mailbox.item.getAttachmentContentAsync(attachment.id, function (asyncResult) {
                        console.log("Item:11221");
                        if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                            attachmentData.push({
                                fileName: attachment.name,
                                fileContent: asyncResult.value.content,  // base64 encoded content
                                fileType: attachment.contentType,
                                fileSize: attachment.size
                            });

                            // check if all attachments are fetched
                            if (attachmentData.length === attachments.length) {
                                resolve(attachmentData);
                            }
                        } else {
                            reject("Failed to get attachment content.");
                        }
                    });
                }
            });

            // if no file attachments found
            if (attachments.length === 0) {
                resolve([]);
            }
        } else {
            reject("No attachments found.");
        }
    });
}

async function sendAttachmentsToServer(attachmentData) {
    const response = await fetch('https://worksorted.com/outlookAddin/webattachmentReceiver.php', {
        method: 'POST',
        headers: {
            "Content-Type": "application/x-www-form-urlencoded"
        },
        body: "attachments=" + encodeURIComponent(JSON.stringify(attachmentData))
    });

    const data = await response.json();

    if (data.status === "success" && data.attachmentIds) {
        return data.attachmentIds;  // return the attachmentIds
    } else {
        throw new Error("No attachment IDs returned from server.");
    }
}

$("#notetype").on("change", function () {
    console.log('Selected value:', document.getElementById("notetype").value);
    const noteType = this.value;
    const clientSelect = document.getElementById("client");
    if (noteType === "0") {
        document.getElementById("job-box").style.display = "none";
        document.getElementById("jobnote-option").style.display = "none";
        document.getElementById("filenote-option").style.display = "block";
        clientSelect.disabled = false;
        document.getElementById("xplan-email-label").disabled = false;
        document.getElementById("saleforce-email-label").disabled = false;
        document.getElementById("midwinter-email-label").disabled = false;
    } else if (noteType === "1") {
        document.getElementById("job-box").style.display = "block";
        document.getElementById("jobnote-option").style.display = "block";
        document.getElementById("filenote-option").style.display = "none";
        const currentGroupId = document.getElementById("group-data").value;
        const currentEntityId = document.getElementById("client").value;
        clientSelect.disabled = false;
        loadClientJob(currentGroupId, currentEntityId);
        document.getElementById("xplan-email-label").disabled = false;
        document.getElementById("saleforce-email-label").disabled = false;
        document.getElementById("midwinter-email-label").disabled = false;
    } else {
        document.getElementById("job-box").style.display = "block";
        document.getElementById("jobnote-option").style.display = "block";
        document.getElementById("filenote-option").style.display = "none";
        const currentGroupId = document.getElementById("group-data").value;
        loadClientJob(currentGroupId, "noEitityId");
        clientSelect.disabled = true;
        document.getElementById("xplan-email-label").disabled = true;
        document.getElementById("saleforce-email-label").disabled = true;
        document.getElementById("midwinter-email-label").disabled = true;
    }
});

function loadClientJob(groupId, entityId) {
    fetch(`https://worksorted.com/outlookAddin/webgetClientJobs.php?groupId=${groupId}&entityId=${entityId}`)
        .then(response => response.json())
        .then(jobs => {
            const jobSelect = document.getElementById("job");
            jobSelect.innerHTML = "<option value=''>Select Job</option>";
            jobs.forEach(job => {
                const option = document.createElement("option");
                option.value = job.jobId;
                option.textContent = job.jobTitle;
                jobSelect.appendChild(option);
            });
            $(jobSelect).select2({
                placeholder: "Search or select a job",
                allowClear: true
            });
        })
        .catch(error => {
            console.error("Error loading jobs:", error);
        });
}

