function sendEmail(emailTitle, emailContent, recipientEmail, id) {
    if (recipientEmail) {
        if (Office.context.mailbox && Office.context.mailbox.item) {
            const item = Office.context.mailbox.item;
            const toRecipients = [recipientEmail];
            item.forward(
                {
                    toRecipients: toRecipients,
                    subject: "Fwd: " + emailTitle + "(ref: " + id + ")",
                    body: "Please see the forwarded email below:\n\n" + emailContent
                },
                function (asyncResult) {
                    if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                        console.log("Email forwarded successfully.");
                    } else {
                        console.error("Failed to forward email:", asyncResult.error.message);
                    }
                }
            );
        }
    }
}
